<template>
  <div class="tab">
    <header>
      <span class="mui-icon mui-icon-back" @click="back"></span>
      <p>项目公告</p>
      <img src="../../img/home/notice.png" alt="">
    </header>
    <section class="myMain">
      <header v-show="isShow">暂无项目公告</header>
      <section v-show="!isShow" class="serve">
        <header></header>
        <section>
          <p></p>
        </section>
      </section>
    </section>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isShow: true
    }
  },
  methods: {
    back(){
      this.$router.go(-1);//返回上一层
    }
  }
}
</script>

<style scoped>
  .tab>header{
    background-color:#024F79;
    color:#fff;
    border-bottom: 1px solid #000;
    font-weight: initial;
    text-align: center;
    /* padding:10px; */
    display: flex;
    justify-content: center;
    align-items: center;
    width:100%;
    position: fixed;
    top:0;    
  }

  .tab>header>p{
    margin:0;
    color:#fff;
    padding: 15px 0 10px;
  }

  .tab>header>img{
    margin-left: 10px;
    width:8%;
    height:8%;
  }  

  .tab,.myMain{
    height:100%;
    width:100%;
    background-color:#fff;
  }

  .myMain>header:first-child{
    text-align: center;
    padding-top:10px;
  }

  /* 项目公告 */
  .myMain,.serve{
    margin-top:40px;
  }

  .serve>header{
    text-align: center;
    /* margin-top:30px; */
    border-bottom: 2px solid #000;
    font-size:16px;
    padding-bottom:5px;
    font-weight: bold;
  }

  .serve>section{
    margin:30px 0;
  }

  .serve>section>p{
    color:#000;
    font-size:12px;
    text-align: justify;
    text-indent: 25.5px;
    /* letter-spacing:2px; */
  }

  .mui-icon{
    position: fixed;
    top:15px;
    left:15px;
    color:#fff;
    font-size: 18px;
  }
</style>



