          <section>
            <p>欢迎您使用绿色桩园服务！我们非常重视您的隐私保护和个人信息保护。本隐私政策适用于您通过任何方式对绿色桩园各项服务的访问和使用。</p>
            <p>您可以利用绿色桩园搜索充电桩、开启充电、定位您所在的位置，进行路径规划、导航您想去的充电场站，充值、充电等（以下统称“绿色桩园产品或服务”）。您具体获得的绿色桩园服务内容可能因为您使用的绿色桩园的版本不同而有所差异。您可以通过多种不同的方式来使用我们的产品和服务，包括绿色桩园的应用程序、微信小程序、微信公众号、支付宝小程序等。</p>
            <p>此隐私权政策旨在帮助您了解我们会收集哪些数据、为什么收集这些数据，会利用这些数据做些什么及如何保护这些数据。请您务必认真阅读本隐私政策，在确认充分了解并同意后使用绿色桩园服务。使用绿色桩园，即表示您同意绿色桩园隐私政策。如果您或您的监护人不同意本隐私政策的任何内容，您应该立即停止使用。</p>
            <p>本隐私政策将帮助您了解以下内容：</p>
            <p>1、我们收集哪些信息，以及我们对这些信息的使用方式</p>
            <p>2、我们如何保存及保护您的个人信息</p>
            <p>3、您的权利</p>
            <p>4、本政策如何更新</p>
            <p>5、如何联系我们</p>
            <p>我们深知个人信息对您的重要性，也深知为您的信息提供有效保护是我们业务健康可持续发展的基石。感谢您对绿色桩园的使用和信任！我们致力于维持您对我们的信任，恪守以下原则，保护您的个人信息：权责一致原则、目的明确原则、选择同意原则、最少够用原则、确保安全原则、主体参与原则、公开透明原则等。</p>
            <p>我们郑重承诺，我们将按业界成熟的安全标准，采取相应的安全保护措施来保护您的个人信息。</p>
            <p>一、我们如何收集和使用您的个人信息</p>
            <p>个人信息是指以电子或者其他方式记录的能够单独或者与其他信息结合识别特定自然人身份或者反映特定自然人活动情况的各种信息。地图数据、场站数据等与您个人无关的信息不属于您的个人信息。</p>
            <p>为了向您提供更好、更个性化的产品和服务，当您使用以下服务、功能或设置时，我们会以下列方式收集和使用您的个人信息：</p>
            <p>1.您直接提供和我们自动采集的个人信息</p>
            <p>(1)您使用绿色桩园业务功能，如充电、充值等，提交与充电及车辆相关的信息；</p>
            <p>(2)您使用绿色桩园服务，可以登录经注册的绿色桩园帐号。当注册账号时，您可能需向我们提供以下信息：手机号码、帐号名称、头像、年龄，并创建密码等。</p>
            <p>绿色桩园会不时针对新推出的服务和功能发起运营活动，如果您参加相关运营活动，我们会通过运营活动界面收集包括但不限于您的身份信息、联系方式、交易帐号类信息，以便与您取得联系，向您发运营奖励等。若您不参加相关运营活动则无需提供前述信息。</p>
            <p>(3)位置信息。当您开启设备定位功能并使用我们基于位置提供的相关服务时，我们可能会收集和处理有关您的设备信息、位置信息、行踪轨迹、网络身份标识信息，以使得您不需要手动输入自身地理坐标就可获得相关服务。我们会使用各种技术进行定位，这些技术包括 IP 地址、GPS 以及能够提供相关信息的其他传感器。您可以通过系统授权关闭定位功能，停止我们对您的地理位置信息的收集，但之后您可能将无法使用相关服务或功能，或者无法达到相关服务拟达到的效果。</p>
            <p>为了向您和其他绿色桩园用户提供实时路况和路况预测，我们会对绿色桩园用户的位置信息进行数据统计分析，并基于此对交通拥堵状况作判断。</p>
            <p>(4)车辆信息。当您使用绿色桩园车主服务时（，您需向我们提供车辆信息（包括但不限于您的车辆品牌、车辆型号、车牌号码、车辆识别代码、发动机号码）。若您不提供上述信息或提供的信息有误，您可能将无法获得相关服务或功能，或者无法达到相关服务拟达到的效果。</p>
            <p>2.我们间接收集的您的信息</p>
            <p>(1)您订购第三方产品或服务的信息。绿色桩园接入了丰富的第三方服务（保险服务等）。当您利用绿色桩园订购上述服务时，可能需要提交您的个人信息。您授权我们将提供该功能所必要的信息提供给为您提供服务的第三方（您的身份信息和位置信息），并授权我们间接向第三方获取必要的相关信息（您的订单信息）。</p>
            <p>(2)为共同向您提供产品/服务或改进产品服务的质量和个性化程度或出于对产品和服务安全性的考量等合理需要，我们将按照法律法规的规定或基于您的授权从关联公司、合作伙伴及其他受信任的第三方供应商处接收您的个人信息及其他信息，并依照您与第三方的约定或本隐私政策使用您的个人信息，两者出现冲突以本隐私政策为准。</p>
            <p>3.事先征得授权同意的例外</p>
            <p>(1)请注意：在以下情形中，收集、使用个人信息无需事先征得您的授权同意：</p>
            <p>a)与国家安全、国防安全直接相关的；</p>
            <p>b)与公共安全、公共卫生、重大公共利益直接相关的；</p>
            <p>c)与犯罪侦查、起诉、审判和判决执行等直接有关的；</p>
            <p>d)出于维护您或其他个人的生命、财产等重大合法权益但又很难得到本人同意的；</p>
            <p>e)所收集的个人信息是您自行向社会公众公开的；</p>
            <p>f)从合法公开披露的信息中收集个人信息的，如合法的新闻报道、政府信息公开等渠道；</p>
            <p>g)根据您的要求向您提供产品或服务所必需的；</p>
            <p>h)用于维护所提供的产品或服务的安全稳定运行所必需的，例如发现、处置产品或服务的故障；</p>
            <p>i)法律法规规定的其他情形。</p>
            <p>4.个人信息的匿名化处理</p>
            <p>在收集到您的个人信息后，我们将通过技术手段及时对数据进行匿名化处理。在不泄露您个人信息的前提下，绿色桩园有权对匿名化处理后的用户数据库进行挖掘、分析和利用（包括商业性使用）。</p>
            <p>5.您授权我们在您使用绿色桩园产品及服务的期间使用您的个人信息，若您注销帐号，我们将停止使用并删除您的个人信息。</p>
            <p>6.设备权限调用</p>
            <p>绿色桩园在提供服务过程中，会调用您的一些设备权限。</p>
            <p>您可以在设备的设置功能中选择关闭部分或全部权限，从而拒绝绿色桩园收集相应的个人信息。</p>
            <p>7.我们可能使用您的信息用于安全防范和诈骗监测等，以预防、发现、调查欺诈、危害安全、非法或违反与我们的协议、政策或规则的行为，以保护您、我们或我们的关联公司、合作伙伴及社会公众的合法权益。</p>
            <p>8.当我们要将信息用于本隐私政策未载明的其它用途时，会事先征求您的同意。当我们要将基于特定目的收集而来的信息用于其他目的时，会事先征求您的同意。</p>
            <p>二、我们如何保存及保护您的个人信息</p>
            <p>1.保存期限。您在使用绿色桩园产品及服务期间，我们将持续为您保存您的个人信息。如果您注销帐户或主动删除上述信息。</p>
            <p>2.保存地域。您的个人信息均储存于中华人民共和国境内，如需跨境传输，我们将会单独征得您的授权同意。</p>
            <p>3.安全措施。</p>
            <p>(1)我们会以“最小化”原则收集、使用、存储和传输用户信息，并通过用户协议和隐私政策告知您相关信息的使用目的和范围。</p>
            <p>(2)我们非常重视信息安全。我们成立了专责团队负责研发和应用多种安全技术和程序等，我们会对安全管理负责人和关键安全岗位的人员进行安全背景审查，我们建立了完善的信息安全管理制度和内部安全事件处置机制等。我们会采取适当的符合业界标准的安全措施和技术手段存储和保护您的个人信息，以防止您的信息丢失、遭到被未经授权的访问、公开披露、使用、毁损、丢失或泄漏。我们会采取一切合理可行的措施，保护您的个人信息。我们会使用加密技术确保数据的保密性；我们会使用受信赖的保护机制防止数据遭到恶意攻击。</p>
            <p>(3)我们会对员工进行数据安全的意识培养和安全能力的培训和考核，加强员工对于保护个人信息重要性的认识。</p>
            <p>(4)我们也请您理解，在互联网行业由于技术的限制和飞速发展以及可能存在的各种恶意攻击手段，即便我们竭尽所能加强安全措施，也不可能始终保证信息的百分之百安全。请您了解，您使用我们的产品和/或服务时所用的系统和通讯网络，有可能在我们控制之外的其他环节而出现安全问题。</p>
            <p>根据我们的安全管理制度，个人信息泄露、毁损或丢失事件被列为最特大安全事件，一经发生将启动公司最高级别的紧急预案，由多个部门组成联合应急响应小组处理。</p>
            <p>4.安全事件通知</p>
            <p>(1)我们会制定网络安全事件应急预案，及时处置系统漏洞、计算机病毒、网络攻击、网络侵入等安全风险，在发生危害网络安全的事件时，我们会立即启动应急预案，采取相应的补救措施，并按照规定向有关主管部门报告。</p>
            <p>(2)个人信息泄露、毁损、丢失属于公司级特大安全事件，我们会负责定期组织工作组成员进行安全预案演练，防止此类安全事件发生。若一旦不幸发生，我们将按照最高优先级启动应急预案，组成紧急应急小组，在最短时间内追溯原因并减少损失。</p>
            <p>(3)在不幸发生个人信息安全事件后，我们将按照法律法规的要求，及时向您告知安全事件的基本情况和可能的影响、我们已采取或将要采取的处理措施、您可自主防范和降低的风险的建议、对您的补救措施等。</p>
            <p>三、您的权利</p>
            <p>按照中国相关的法律、法规、标准，以及其他国家、地区的通行做法，我们保障您对自己的个人信息行使以下权利：</p>
            <p>1、访问权。原则上您可以通过如下方式访问您的个人信息：帐户信息：您可以随时登录绿色桩园app，访问或编辑您的帐户中的个人资料信息、更改您的密码、添加安全信息、进行账户关联或身份认证等。</p>
            <p>2、更正权。当您发现我们处理的关于您的个人信息有错误时，经对您的身份进行验证，且更正不影响信息的客观性和准确性的情况下，您有权对错误或不完整的信息作出更正或更新，您可以自行在绿色桩园内进行更正，或通过反馈与报错等将您的更正申请提交给我们。</p>
            <p>3、删除权。在以下情形中，您可以向我们提出删除个人信息的请求：</p>
            <p>(1)如果我们终止绿色桩园的产品运营及服务；</p>
            <p>(2)如果您注销了帐号，我们会删除您的个人信息或做匿名化处理；</p>
            <p>(3)涉及商业秘密的。</p>
            <p>4、提前获知产品和服务停止运营权。绿色桩园愿一直陪伴您，若因特殊原因导致绿色桩园产品和服务被迫停止运营，我们将提前向您发送短信或其他合适的能触达您的方式通知您，并将停止对您个人信息的收集，同时会按照法律规定对所持有的您的个人信息进行删除或匿名化处理等。为保障安全，您可能需要提供书面请求，或以其他方式证明您的身份。我们可能会先要求您验证自己的身份，然后再处理您的请求。我们将在三十天内做出答复。</p>
            <p>5、在以下情形中，按照法律法规要求，我们将无法响应您的更正、删除、注销信息的请求：</p>
            <p>(1)与国家安全、国防安全直接相关的；</p>
            <p>(2)与公共安全、公共卫生、重大公共利益直接相关的；</p>
            <p>(3)与犯罪侦查、起诉、审判和执行判决等直接相关的；</p>
            <p>(4)我们有充分证据表明您存在主观恶意或滥用权利的（如您的请求将危害公共安全和其他人合法权益，或您的请求超出了一般技术手段和商业成本可覆盖的范围）；</p>
            <p>(5)响应个人信息主体的请求将导致您或其他个人、组织的合法权益受到严重损害的；</p>
            <p>(6)涉及商业秘密的。</p>
            <p>四、隐私政策的修订</p>
            <p>我们的隐私政策可能变更。</p>
            <p>未经您明确同意，我们不会削减您按照本隐私政策所应享有的权利。我们会发布对本隐私政策所做的任何变更。</p>
            <p>若您不同意该等变更可以停止使用绿色桩园产品和服务，若您继续使用我们的产品和/或服务，即表示您同意受修订后的本隐私政策的约束。</p>
            <p>本政策所指的重大变更包括但不限于：</p>
            <p>1、我们的服务模式发生重大变化。如处理个人信息的目的、处理的个人信息类型、个人信息的使用方式等；</p>
