<template>
  <div>
    <!-- 标题 -->
    <header class="myTitle">
      <img src="../../img/home/juzhongli_first_logo_1.png" alt="">
      <div class="mui-input-row mui-search">
        <input type="search" class="mui-input-clear" placeholder="关键词搜索">
      </div>        
    </header>
    <!-- 轮播图 -->
    <swiper-box :imagelist="imagelist">
    </swiper-box>
    <!-- nav -->
    <div class="nav">
      <!-- <mt-button size="small" @click.native.prevent="active = 'tab-container1'">即将上新</mt-button>
      <mt-button size="small" @click.native.prevent="active = 'tab-container2'">现在开放</mt-button>
      <mt-button size="small" @click.native.prevent="active = 'tab-container3'">停止出让</mt-button> -->
      <span @click="jump(1)" :class="navClass1">即将上新</span>
      <!-- <span @click="jump(2)" :class="navClass2">现在开放</span>
      <span @click="jump(3)" :class="navClass3">停止出让</span> -->
    </div>
    <div class="page-tab-container">
      <mt-tab-container class="page-tabbar-tab-container" v-model="active" swipeable>
        <mt-tab-container-item id="tab-container1">
          <div>
            <img src="../../img/home/WTS_firstpage_main.png" alt="" style="width:100%" @click="details()">
            <ul>
              <li v-for="item in label" :key="item.lab" v-html="item.lab"></li>
            </ul>
          </div>
          <p>深圳罗湖区梧桐山公园停车场充电站（第一期）</p>
          <p>一期标的出让股权比例：32.5%  每1%股权价格：12万</p>
          <p>一期出让股权总金额：390万</p>
          <div id="demo1" class="mui-text-center">
            <!-- <p class="mui-progressbar mui-progressbar-in" :data-progress="progress" style="margin-bottom:0;"><span></span></p> -->
            <progress :value="progress"></progress>
            <span>认筹完成度：80%</span>
          </div>
          <p>剩余天数：5天</p>
          <img src="../../img/home/WTS_firstpage_main.png" alt="" style="width:100%">
        </mt-tab-container-item>
        <!-- <mt-tab-container-item id="tab-container2">
          
        </mt-tab-container-item>
        <mt-tab-container-item id="tab-container3">

        </mt-tab-container-item> -->
      </mt-tab-container>
    </div>   
  </div>
</template>

<script>
//1:导入图片轮播组件
import  swiper from "../subcomponents/swiper.vue";

export default {
  name: 'page-tab-container',
  data(){
    return{
      imagelist:[
        {img_url:'https://gw.alipayobjects.com/os/q/cms/images/jvf26g84/b3a61c8f-77c5-49ec-b27e-53f100a4b35e_w750_h298.jpg'},
        {img_url:'https://gw.alipayobjects.com/os/q/cms/images/jvf26nsn/cd56a348-2a52-453a-9ed4-ec20517f068a_w750_h298.jpg'},
        {img_url:'https://gw.alipayobjects.com/os/q/cms/images/jvf26xbg/2a76f6f0-efde-4424-a7ae-3619d3391f81_w750_h298.jpg'}
      ],
      active: 'tab-container1',
      navClass1: 'aclickNav',
      navClass2: '',
      navClass3: '',
      progress: 0.5,
      label: [
        {lab:"惠能系"},
        {lab:"10年租期"},
        {lab:"的士村"}
      ]
    }
  },
  mounted(){
    // var progressbar1 = mui('#demo1');
    // mui(progressbar1).progressbar().setProgress(this.progress)
    // progressbar1.on('tap', 'a', function() {
    //   mui(progressbar1).progressbar().setProgress(this.getAttribute('data-progress'));
    // });
  },
  methods:{
    jump(index){
      this.navClass1 = this.navClass2 = this.navClass3 = '';      
      if(index == 1){
        this.active = 'tab-container1';
        this.navClass1 = 'aclickNav';
      }else if(index == 2){
        this.active = 'tab-container2';
        this.navClass2 = 'aclickNav';
      }else{
        this.active = 'tab-container3';
        this.navClass3 = 'aclickNav';
      }
    },
    details(){
      this.$router.push("/home/projectdetails");
    }
  },
  //2.注册子组件
  components:{
    "swiper-box":swiper
  }
}
</script>

<style scoped>
  .item {
      display: inline-block;
    }

  .nav {
    padding: 10px;
  }

  .link {
    color: inherit;
    padding: 20px;
    display: block;
  }
  
  .nav>span{
    font-size:14px;
    margin:0 5px;
  }

  .aclickNav{
    color:#0B557D;
    border-bottom:1px solid #0B557D;
  }

  .mint-tab-container-item>p{
    font-size:12px;
    margin:0;
    color:#000;
  }

  .mint-tab-container-item{
    padding:0 10px;
  }

  /* .mint-tab-container-item>img{
    height:149px;
    width:340px!important;
    margin:0 auto;
  } */

  /* 进度条 */
  .mui-progressbar span {
    background: red;
  }

  #demo1>p:first-child{
    display: inline-block;
    width:60%;
  }

  .mui-progressbar{
    background-color:#24A4D1;
  }

  #demo1{
    text-align: left!important;
  }

  #demo1>progress{
    margin-right:10px;
  }

  /* 标题部分 */
  .myTitle{
    text-align: center;
    padding:15px 0 10px;
    background-color:#024F79;
  }

  .myTitle>img{
    width:25%;
  }

  .mui-search {
    display: inline-block;
    width: 90%;
  }

  input[type='search'] {
    border-radius: 50px;
    background:#fff;
    margin-bottom: 0;
  }

/* 改变放大镜激活之后的位置 */
  .mui-search.mui-active:before {
    top:22px;
  }

/* 图片上的标签 */
  .mint-tab-container-item>div:first-child{
    position: relative;
  }

  .mint-tab-container-item>div:first-child>ul{
    position: absolute;
    top:6px;
    left:15px;
  }

  .mint-tab-container-item>div:first-child>ul>li{
    color:#fff;
    background-color:#B5B5B6;
    padding:0px 6px;
    border-radius:50px;
    margin-right:5px;
    display: inline-block;
  }

</style>



