<template>
  <div class="equity">
    <header class="myTitle">股权结构</header>
    <div class="stock">
      <header>
        <p>项目名称：{{projectName.name}}</p>
        <nav>
          <span :class="place.pitchOn1" @click="pitchOn(0)">股权结构</span>|<span :class="place.pitchOn2" @click="pitchOn(1)">转让记录</span>
        </nav>
        <section v-show="place.isShow">
          <header class="left1"></header>
          <header class="left2"></header>
          <div v-for="item2 in projectName.equityStructure" :key="item2.key">
            <span v-html="item2.company"></span>
            <span v-html="item2.stock"></span>
          </div>
        </section>
        <section v-show="!place.isShow">
          <header class="right1"></header>
          <header class="right2"></header>
          <section  v-for="item3 in projectName.record" :key="item3.key">
            <section>
              <span v-html="item3.A"></span><span v-html="item3.date"></span>
            </section>
            <section>
              <span v-html="item3.B"></span><span v-html="item3.stock"></span>
            </section>
          </section>
        </section>
      </header>
    </div>    
  </div>
</template>

<script>
export default {
  data() {
    return {
      projectName:{
        name:'第一桶金',
        equityStructure:[
          {company:'梧桐山充电管理运营公司',stock:'25%'},
          {company:'罗湖梧桐山公园',stock:'25%'},
          {company:'某某有限合伙企业',stock:'32.5%'},
          {company:'绿色桩园(深圳)科技有限公司',stock:'17.5%'}
        ],
        record:[
          {A:'深圳阿士大夫有限公司',date:'2019-09-09',B:'转给深圳绿色桩园有限公司',stock:'0.01%'}
        ]
      },
      place:{pitchOn1:'pitchOn',pitchOn2:'',isShow:true}
    }
  },
  beforeMount() {
 
  },
  methods: {
    pitchOn(num){
      if(num == 0){
        this.place.pitchOn1 = 'pitchOn';
        this.place.pitchOn2 = '';
        this.place.isShow = true;
      }else{
        this.place.pitchOn1 = '';
        this.place.pitchOn2 = 'pitchOn';
        this.place.isShow = false;
      }
    }
  }
}
</script>

<style scoped>
/* 股权结构页面 */
  .equity{
    width:100%;
    height:100%;
    background-repeat: no-repeat;
    background-size:contain;
    background-attachment: fixed;
    background-image: url("../../img/Project/background_login_2.jpg");
    text-align: center;
  }

  .stock{
    /* height: 667px; */
    color: #fff;
    font-size: 12px;
    overflow: auto;
    padding:10px 0;
    display: inline-block;
    width:80%;
    height:100%;
  }

  .myTitle{
    width:100%;
    margin: 0;
    color: #fff;
    padding: 15px 0 10px;
    background-color: #024F79;
    text-align: center;
    font-size: 14px;
  }

  .stock>header{
    padding:30px 0;
  }

  .stock>header>p{
    color: #fff;
    font-size:16px;
    text-align: left;
  }

  .stock>header>nav{
    text-align: center;
    font-size:16px;
    /* font-weight: bold; */
    background-color:#206EC0;
    border:1px solid #5A94D1;
    border-radius:5px;
    padding:5px 0;
  }

  .stock>header>nav>span{
    padding:0 15px;
  }

  .stock>header>section{
    margin-top:15px;
    background-color:#206EC0;
    border:1px solid #5A94D1;
    border-radius:5px;
    padding:25px 15px; 
  }

  .stock>header>section>div,.stock>header>section>section>section{
    display: flex;
    justify-content: space-between;
  }

  .stock>header>section>section{
    padding: 10px 0;
    border-bottom: 1px solid #fff;
  }

  .stock>header>section{
    position: relative;
  }

  .stock>header>section>header{
    width: 0px;
    height: 0px;
    position: absolute;
  }

  .stock>header>section>header:first-child{
    border: 8px solid transparent;
    border-bottom-color: #206EC0;
    z-index:910;
    top:-16px;
  }

  .stock>header>section>header:nth-child(2){
    border: 10px solid transparent;
    border-bottom-color: #5A94D1;
    z-index:905;
    top:-20px;
  }  

  .left1{
    left:100px;
  }

  .left2{
    left:98px;
  }

  .right1{
    right:100px;
  }

  .right2{
    right:98px;
  }

  .pitchOn{
    font-weight: bold;
  }
</style>



