<!--将图片轮播封装子组件中--之所以封装图片轮播和封装评论子组件原因不同，图片轮播一个项目中可能多次使用，为了不要重复写多次，因此封装起来，然后通过id引用即可，一劳永逸-->
<template>
    <div class="box">
        <!--图片轮播-->
        <mt-swipe :auto="4000">
            <mt-swipe-item v-for="item in imagelist" :key="item.id">
                <img :src="item.img_url">
            </mt-swipe-item>
        </mt-swipe>
    </div>
</template>

<script>
    export default{
        data(){
            return{}
        },
        methods:{

        },
        props:["imagelist"]//父元素传过来的数据imagelist，父元素给什么它就循环什么
    }
</script>

<style>
    /*修改图片轮播高度*/
    .box .mint-swipe{
        height: 149px;
    }
    /*内容居中*/
    .box .mint-swipe .mint-swipe-item{
        text-align:center;
    }
    /*修改图片轮播中图片的样式  自适应宽度*/
    .box .mint-swipe .mint-swipe-item img{
        width:100%;
        height:100%;
    }

    .mint-swipe-indicators{
        display: none;
    }
</style>


