            <p>2.4 深圳市交警局数据，全市新能源汽车保有量已近22万辆。2018年新能源汽车全年注册登记数量约为7.5万辆，超过上海和北京位居全国第一。</p>
            <p><span>2018年9月，深圳巴士集团在深圳地区的4681台出租车全部置换为纯电动出租车，成为全国最大的纯电动出租车运营集团。截至2018年9月底，深圳市投放纯电动出租车20114辆，纯电动率已达93.85%。</span></p>
            <p>【场站优势】</p>
