            <p>司机聚集地：<span>附近大望村为城中村，房租便宜，为网约车、出租车司机聚集地。约3公里处已建成的沙湾吉厦充电站年化收益超60%。</span></p>
            <p>交通优势：<span>距离深圳东火车站7公里，距离深圳罗湖火车站9公里，临近东部过境高速。</span></p>
