            <p>五、【附属细则】</p>
            <p>保护性规定:<span>采取以下任何行动需征得场站其他股东的事先同意：清算、合并、兼并、解散；修改、变更投资者权利或可能影响投资者权利的事项；购买、转让或赎回股权；公司不接受公司的股权作为标的质押给公司，但可以质押给他人或其他机构。</span></p>
            <p>知情权:<span>公司应定期提供财务报表及投资者可能合理要求的其他信息，一旦发生任何重大变化，应及时通知投资者。</span></p>
            <p>跟投权: <span> 如公司未来进行增资(向员工发行的期权和股权除外)，投资者有权按其持有的股权比例以同等条件和价格认购该股权。</span></p>
            <p>投资者权利: <span>投资者应享有不劣于公司现有及未来股东投资人的股东权益，包括但不限于优先购买权、共售权、分红权、转换权、上市登记权等权利。</span></p>
            <p>优先投资权: <span>若交割后合作方开展场站建设新项目，投资者在同等条件下对该等新项目享有优先投资的权利。</span></p>
