      active: '',
      sign:[
        {}
      ]
    }
  },
  created(){
    this.jump(0);
  },
  mounted(){
    //1:获取最外层的div绑定点击事件，播放视频
    var box=document.getElementById("box");
    var v3=document.getElementById("v3");
    //2:全屏播放时，安卓手机有位置不确定的情况
    //x5-video-player-fullscreen="true" 
    //x5-video-orientation="portraint"  //这两个属性写上去不会完成所有的功能，要配合JS使用全屏设置对监听，绑定resize事件
    //1:为窗口绑定resize事件，重新调整窗口位置
    window.onresize=function(){
    //2:重新给div赋值
      box.style.width=window.innerWidth+"px";
      box.style.height=window.innerHeight+"px";
    }

    //3:手机手指滑动操作，阻止默认行为
    document.addEventListener("touchmove",function(e){
      e.preventDefault()
    },false);
    //4:在某些手机浏览器--视频高度
    //重新设置div高度
    var _h=document.body.cilentHeight;
    box.style.height=_h;
    //5:事件 timeupdate(当播放时间发生变化时自动触发这个事件)
    //当前视频播放触发事件，只要视频在播放，会一直触发；可以获取当前视频的播放进度
    // v3.addEventListener("timeupdate",function(){//这个可以看到当前视频播放的时间，后面配上样式，可以做视频的进度条
    //   console.log("当前播放时间:"+v3.currentTime);
    // })
    // //6:事件 canplaythrough     视频加载成功
    // v3.addEventListener("canplaythrough",function(){
    //   console.log("canplaythrough");
    //   console.log("视频总时长:"+v3.duration);
    // })
    // //7:事件 ended  播放结束
    // v3.addEventListener("ended",function(){
    //   console.log("播放结束");
    // })

    // 进度条部分
    // var progressbar1 = mui('#demo1');
    // mui(progressbar1).progressbar().setProgress(this.progress)
    // progressbar1.on('tap', 'a', function() {
    //   mui(progressbar1).progressbar().setProgress(this.getAttribute('data-progress'));
    // });
  },
  methods:{
    jump(index){
      for(let i=0;i<5;i++){//以后有多少个navbar就把5改成多少个即可
        this.navClass.push({navb: 'underline',active: 'tab-container'+(i+1)});
        this.navClass[i].navb = '';
      }
      this.navClass[index].navb = 'underline';
      this.active = this.navClass[index].active;
    }
  }
}
