import Vue from 'vue'
import Router from 'vue-router'
import Register from '@/components/Register/Register.vue'
import Home from '@/page/home'
import HomePage from '@/components/Home/Home'
import ProjectDetails from '@/components/Home/ProjectDetails'
import Issue from '@/components/Tabbar/Issue'
import Notice from '@/components/Tabbar/Notice'
import Our from '@/components/Tabbar/Our'
import EquityStructure from '@/components/myProject/equityStructure'

Vue.use(Router)

export default new Router({
  // mode: 'history',//去除路由器默认携带的#  
  routes: [
    {
      path: '/',
      redirect:"/register"//重定向
    },
    {
      path: '/register',
      name: 'register',
      component:Register
    },
    {
      path: '/home',
      name: 'home',
      component:Home,
      children:[
        { path: "",name: 'homepage',component: HomePage },
        { path: "projectdetails",name: 'projectdetails',component: ProjectDetails },
        { path: "issue",name: 'issue',component: Issue },
        { path: "notice",name: 'notice',component: Notice },
        { path: "our",name: 'our',component: Our },
        { path: "equityStructure",name: 'equityStructure',component: EquityStructure },
      ]
    }
  ]
})



